<template>
  <button class="theme-switcher" @click="toggleTheme">
    Сменить тему
  </button>
</template>

<script>
export default {
  name: 'ThemeSwitcher',
  methods: {
    toggleTheme() {
      document.documentElement.classList.toggle('dark');
    },
  },
};
</script>

<style scoped>
.theme-switcher {
  color: var(--text-color);
  cursor: pointer;
  background: none;
  border: var(--border-color) solid 2px;
  border-radius: 5px;
  padding: 2px 5px;
  position: fixed;
  margin-top: 10px;
  margin-right: 10px;
  right: 0;
  top: 0;
}
</style>
