import Vue from 'vue';
import VueRouter from 'vue-router';
import Bus from '../views/Bus.vue';
import Cpu from '../views/Cpu.vue';
import Main from '../views/Main.vue';

Vue.use(VueRouter);

const routes = [
  {
    path: '/',
    component: Main,
  },
  {
    path: '/bus',
    component: Bus,
  },
  {
    path: '/cpu',
    component: Cpu,
  },
];

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes,
});

export default router;
