<template>
  <div class="cpu">
    <page-heading>Процессор</page-heading>
    <page-thesis>Определение</page-thesis>
    <page-text>
      Центра́льный проце́ссор (ЦП; также центра́льное проце́ссорное устро́йство — ЦПУ; англ. central processing unit, CPU, дословно — центральное обрабатывающее устройство, часто просто процессор) — электронный блок либо интегральная схема, исполняющая машинные инструкции (код программ), главная часть аппаратного обеспечения компьютера или программируемого логического контроллера. Иногда называют микропроцессором или просто процессором.
    </page-text>
    <page-thesis>Архитектура фон Неймана</page-thesis>
    <page-text>
      Большинство современных процессоров для персональных компьютеров в общем основано на той или иной версии циклического процесса последовательной обработки данных, изобретённого Джоном фон Нейманом.
    </page-text>
    <page-text>
      Дж. фон Нейман придумал схему постройки компьютера в 1946 году.
    </page-text>
    <page-text>
      Отличительной особенностью архитектуры фон Неймана является то, что инструкции и данные хранятся в одной и той же памяти.
    </page-text>
    <page-text>
      В различных архитектурах и для различных команд могут потребоваться дополнительные этапы. Например, для арифметических команд могут потребоваться дополнительные обращения к памяти, во время которых производится считывание операндов и запись результатов.
    </page-text>
    <page-text>
      Этапы цикла выполнения:
    </page-text>
    <page-list>
      <page-list-item>Процессор выставляет число, хранящееся в регистре счётчика команд, на шину адреса и отдаёт памяти команду чтения.</page-list-item>
      <page-list-item>Выставленное число является для памяти адресом; память, получив адрес и команду чтения, выставляет содержимое, хранящееся по этому адресу, на шину данных и сообщает о готовности.</page-list-item>
      <page-list-item>Процессор получает число с шины данных, интерпретирует его как команду (машинную инструкцию) из своей системы команд и исполняет её.</page-list-item>
      <page-list-item>Если последняя команда не является командой перехода, процессор увеличивает на единицу (в предположении, что длина каждой команды равна единице) число, хранящееся в счётчике команд; в результате там образуется адрес следующей команды.</page-list-item>
    </page-list>
    <page-subtesis>Конвейерная архитектура</page-subtesis>
    <page-text>
      Конвейерная архитектура (англ. pipelining) была введена в центральный процессор с целью повышения быстродействия. Обычно для выполнения каждой команды требуется осуществить некоторое количество однотипных операций, например: выборка команды из ОЗУ, дешифровка команды, адресация операнда в ОЗУ, выборка операнда из ОЗУ, выполнение команды, запись результата в ОЗУ. Каждую из этих операций сопоставляют одной ступени конвейера. Например, конвейер микропроцессора с архитектурой MIPS-I содержит четыре стадии:
    </page-text>
    <page-list>
      <page-list-item>получение и декодирование инструкции</page-list-item>
      <page-list-item>адресация и выборка операнда из ОЗУ</page-list-item>
      <page-list-item>выполнение арифметических операций</page-list-item>
      <page-list-item>сохранение результата операции</page-list-item>
    </page-list>
    <page-text>После освобождения k-й ступени конвейера она сразу приступает к работе над следующей командой. Если предположить, что каждая ступень конвейера тратит единицу времени на свою работу, то выполнение команды на конвейере длиной в n ступеней займёт n единиц времени, однако в самом оптимистичном случае результат выполнения каждой следующей команды будет получаться через каждую единицу времени.</page-text>
